import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { LoginService } from './login.service';
import { Router } from '@angular/router';
import { User } from './object-user';
import { of, Observable } from 'rxjs';
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  users: User[];
  rForm: FormGroup;
  email: string;
  password: string;
  isLogin: boolean = true;
  isLoading: boolean = false;
  errorAlert: string = "Please enter your email!";
  constructor(
    private loginService: LoginService,
    private formBuilder: FormBuilder,
    private router: Router
  ) {

    this.rForm = formBuilder.group({
      email: [null, Validators.compose([Validators.email, Validators.required])],
      password: [null],
      checkbox: true
    })
  }

  ngOnInit() {
  }
  getAllUsers(email, password) {
    this.isLoading = true;
    this.loginService.getUsers()
      .subscribe(users => {
        this.checkLogin(users, email, password);
        this.isLoading = false;
      }, err => {
        alert('ERROR 404');
        this.isLoading = false;
      }
      );

  }
  checkLogin(users: User[], email: string, password: string) {
    var user: any;
    for (user of users) {
      if (email == user.email && password == user.password) {
        this.router.navigate(['/admin']);
        this.isLogin = true;
        return true;
      }
      this.errorAlert = "Email or password was wrong!";
      this.isLogin = false;
      return false;
    }
  }
  writeUp(): void {
    this.errorAlert = "Please enter your email!";
    this.isLogin = true;
  }
  submitForm(post) {
    this.getAllUsers(post.email, post.password);
  }
}